.movie-card {
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    border: 0;
    display: block;
}

.movie-card img {
    width: 100%;
    display: block;
}

.movie-card .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid transparent;
    transition: all 0.3s ease;
}

.movie-card:hover .overlay {
    border: 3px solid var(--secondary);
}
